.hidden {
  display: none;
}

.city-selector {
  position: absolute;
  top: 0;
  left: 0;
  min-height: 100%;
  background-color: #fff;
  width: 100%;
  z-index: 1000;
}

.city-search {
  position: relative;
  height: 34px;
  line-height: 34px;
  width: 100%;
  background: #00a8bc;
  color: #fff;
  font-size: 18px;
  text-align: center;
  padding: 5px 0;
}

.search-back {
  position: absolute;
  top: 0;
  left: 0;
  width: 42px;
  height: 42px;
  cursor: pointer;
}

.search-input-wrapper {
  font-size: 12px;
  height: 32px;
  border-radius: 5px;
  margin-left: 42px;
  margin-right: 15px;
  padding-left: 5px;
  background: #fff;
  text-align: left;
}

.search-input {
  width: 80%;
  font-size: 12px;
  height: 32px;
  border: 0;
  color: #333;
  outline: 0;
  background: 0;
  vertical-align: top;
  line-height: 1.5;
  padding: 0;
  padding-left: 5px;
}

@font-face {
  font-family: yofont;
  src: url(data:font/woff;base64,)
    format('woff');
}

.search-clean {
  position: absolute;
  right: 20px;
  top: 4px;
  color: #ccc;
  font-family: 'yofont';
  font-style: normal;
}

.city-list {
  background: #fff;
}

.city-content {
}

.city-title {
  background: #eee;
  padding-top: 15px;
  padding-bottom: 15px;
  padding-left: 10px;
}

.city-section {
  padding: 0;
}

.city {
  padding: 12px 10px;
  list-style: none;
  border-bottom: 1px solid #eee;
  padding-left: 30px;
}

.alpha-bet {
  position: fixed;
  top: 50%;
  right: 0;
  transform: translateY(-50%);
  .alpha-item {
    list-style: none;
    font-size: 12px;
    color: #a5a5a5;
    padding: 4px;
  }
}
